<template>
    <div>

        <v-container>
            <v-row no-glutters>
                <v-col cols="2" class="links">
                    Links
                </v-col>

                <v-col cols="12" md="7">
                    <h3>
                        Resultados para: {{ this.query }}
                    </h3>

                    <div v-for="(problematica, index) in results" :key="index">
                        <div id="app" class="tarjeta">
                            <v-app id="inspire">
                                <v-card class="mx-auto my-12" max-width="90%">
                                    <v-img height="250" v-bind:src="problematica.imagen"></v-img>
                    
                                    <v-card-title>{{ problematica.titulo }}</v-card-title>
                                    
                                    <v-card-text>
                                        <v-row align="center" class="mx-0">
                                            <v-rating :value="4.5" color="amber" dense half-increments readonly size="14"></v-rating>
                                    
                                            <div class="grey--text ml-4">4.5</div>
                                        </v-row>
                                    
                                    </v-card-text>

                                    <v-card-text>
                    
                                        <div class="my-4 subtitle-1">
                                            <b>Ubicación: </b>
                                            {{ problematica.ubicacion }}
                                        </div>
                    
                                    </v-card-text>
                    
                                    <v-divider class="mx-4"></v-divider>
                    
                    
                    
                                    <v-card-actions>
                                        <v-btn color="deep-purple lighten-2" text @click="redirect_to_problematica(problematica.id)">
                                            Ver más
                                        </v-btn>
                                    </v-card-actions>
                                </v-card>
                            </v-app>
                        </div>
                    </div>

                </v-col>
                
                <v-col cols="2" class="links">
                    Links
                </v-col>
            </v-row>
        </v-container>

        
    </div>
</template>

<script>
    import axios from 'axios'

    export default{
        data() {
            return {
                results: [],
                username: this.$store.state.username,
                user_is_logged: this.$store.state.user_is_logged
            }
        },
        props: {
            query: String
        },
        created() {
            this.getUsers(this.query)
        },
        watch: {
            query: function(newQuery){
                this.getUsers(newQuery)
            }
        },
        methods: {
            getUsers(search){
                axios.get(this.$store.state.api + 'problematicas/?search=' + search)
                .then((data) => {
                    this.results = []
                    data.data.results.map((result) => {
                        this.results.push(result)
                    })
                }).catch((error) => {
                    console.log("Ha ocurrido un error");
                    console.log(error);
                })
            },

            redirect_to_problematica(id) {
                this.$router.push({ name: "problematicas", params: { id: id } })
            }
        },
    }
</script>