<template>
    <div>
        <div v-for="(problematica, index) in problematicas" :key="index">
            <div id="app" class="tarjeta">
                <v-app id="inspire">
                    <v-card class="mx-auto my-12" max-width="90%">
                        <v-img height="250" v-bind:src="problematica.imagen"></v-img>
        
                        <v-card-title>{{ problematica.titulo }}</v-card-title>
                        
                        <v-card-text>
                            <v-row align="center" class="mx-0">
                                <v-rating :value="4.5" color="amber" dense half-increments readonly size="14"></v-rating>
                    
                                <div class="grey--text ml-4">4.5</div>
                            </v-row>

                        </v-card-text>

                        <v-card-text>
        
                            <div class="my-4 subtitle-1">
                                <b>Ubicación: </b>
                                {{ problematica.ubicacion }}
                            </div>
        
                        </v-card-text>
        
                        <v-divider class="mx-4"></v-divider>
        
        
        
                        <v-card-actions>
                            <v-btn color="deep-purple lighten-2" text @click="redirect_to_problematica(problematica.id)">
                                Ver más
                            </v-btn>
                        </v-card-actions>
                    </v-card>
                </v-app>
            </div>
        </div>
        <v-banner two-line class="mb-5" v-if="end">
            <v-avatar slot="icon" color="deep-purple accent-4" size="40">
                <v-icon icon="mdi-lock" color="white">
                    mdi-checkbox-marked-circle
                </v-icon>
            </v-avatar>
        
            <h4>
                Has visto todos los proyectos
            </h4>
        
        </v-banner>
    </div>
</template>

<script>
    import axios from 'axios';
    export default{
        data(){
            return{
                problematicas: [],
                busy: false,
                currentPage: 1,
                end: false,
                selection: 1,
            }
        },
          methods: {
                    getProblematicas() {
                        // Cargando los posts
                        if (this.currentPage === -1)
                            return
                        this.busy = true
                        axios.get(this.$store.state.api + 'problematicas/?page=' + this.currentPage, {
                            headers: {
                                Authorization: 'Token ' + this.$store.state.token
                            }
                        }).then((response) => {
                            response.data.results.map((problematica) => {
                                this.problematicas.push(problematica)
                            })
                            if (response.data.next != null)
                                this.currentPage += 1
                            else {
                                this.currentPage = -1
                                this.end = true
                            }
                            this.busy = false
                        }).catch((error) => {
                            console.log("Ha ocurrido un error");
                            console.log(error);
                            this.busy = false
                        })
                    },

                    redirect_to_problematica(id){
                        this.$router.push({ name: "problematicas", params: { id: id } })
                    }
                    
                },

    created() {
            this.getProblematicas()
        },
    }
</script>