<script>
    import axios from 'axios'
    export default{
        data() {
            return {
                id: 0,
                problematica: [],
                selection: 0,
                loading: false,
                url: "",
                habilitado: true,
                dialog: false
            }
        },
        created() {
            this.id = this.$route.params.id;
            if(this.$store.state.token.length <= 0){
                this.habilitado = false;
            }
            this.url = "http://localhost:8080/problematicas/" + this.id;
            this.getProblematica(this.id);
        },
        methods: {
            getProblematica(id){
                axios.get(this.$store.state.api + 'problematicas/' + id + '/')
                .then((result) => {
                    this.problematica = result.data;
                    console.log(this.problematica);
                }).catch((error) => {
                    console.log("Ha ocurrido un error");
                    console.log(error);
                })
            },
            
            reserve(){
                var info = {user: this.$store.state.username}
                var data = new FormData();
                data.append("json", JSON.stringify(info))
                fetch(this.$store.state.api + 'send_mail/', {
                    method: "POST",
                    body: data
                })
                .then((response) => response.json())
                .then((data) => {
                    console.log(data);
                    console.log("Todo bien");
                }).catch((error) => {
                    console.log("Ha ocurrido un error");
                    console.log(error);
                })
                console.log("Reservando...");
            }
        },
    }
</script>