<template>
    <v-card color="grey lighten-4" flat tile>
        <v-toolbar dense :dark="true" height="60">

            <v-toolbar-title class="title" @click="redirect_to_feed_or_login">Grinmatch</v-toolbar-title>
    
            <v-spacer></v-spacer>
            
            <form class="form mt-7" @submit.prevent="search_user" style="width: 75%;">
                <v-text-field id="search" v-model="search" label="Busca un proyecto..." solo-inverted autocompete="off"></v-text-field>
                <v-btn type="submit" icon>
                    <v-icon>mdi-magnify</v-icon>
                </v-btn>
            </form>

            
            <template v-slot:activator="{on, attrs}">
                <v-btn icon v-bind="attrs" v-on="on">
                    <v-icon>mdi-dots-vertical</v-icon>
                </v-btn>
            </template>
            


            <v-menu transition="slide-y-transition" bottom>
                <template v-slot:activator="{ on, attrs }">
                    <v-btn icon v-bind="attrs" v-on="on">
                        <v-icon>mdi-dots-vertical</v-icon>
                    </v-btn>
                </template>
                <v-list>
                    <v-list-item class="items">
                        <v-list-item-title @click="redirect_to_educacion" >Educación ambiental</v-list-item-title>
                    </v-list-item>
                    <v-list-item class="items">
                        <v-list-item-title @click="redirect_to_inicia" >Inicia sesion</v-list-item-title>
                    </v-list-item>
                    <v-list-item class="items">
                        <v-list-item-title @click="redirect_to_profile" >Mi perfil</v-list-item-title>
                    </v-list-item>
                    <v-list-item class="items">
                        <v-list-item-title @click="logout">Logout</v-list-item-title>
                    </v-list-item>
                </v-list>
            </v-menu>

        
                

            

        </v-toolbar>
    </v-card>
</template>

<script>
    export default{
        data() {
            return {
                items: [
                    {title: "My profile"},
                    {title: "Logout"}
                ],
                search: ""
            }
        },
        created() {
        },

        methods: {
            logout() {
                this.$store.commit('update_auth_token', '')
                this.$store.commit('set_username', '')
                this.$store.commit('set_user_is_logged', false)
                this.$router.push({ name: "Login" })
            },
            
            redirect_to_profile(){
                this.$router.push({ name: "profile", params: { username: this.$store.state.username } })
            },
            redirect_to_educacion(){
                this.$router.push({ name: "educacion"});
            },
            redirect_to_inicia(){
                this.$router.push({ name: "Login"});
            },

            redirect_to_feed_or_login(){
                if(this.$store.state.user_is_logged)
                    this.$router.push({ name: "landing" })
                else 
                    this.$router.push({ name: "landing" })
            },

            search_user(){
                this.$router.push({name: "search", query: {q: this.search}})
            }
        },
    }
</script>

<style>
    .items, .title{
        cursor: pointer;
    }
    .form{
        display: flex;
    }
    #search{
        padding: 0 !important;
    }
</style>